.primary-btn{
  color:white;
  border: 2px solid linen;
  font-size: 16px; 
  background-color: #1f2235;
}
.primary-btn:hover{
  color: aliceblue;
  border: 2px solid #ff5823;
  background-color: #1f2235;
}
.highlighted-btn{
  color: white;
  background-color: #ff5823;
}
.highlighted-btn:hover{
  color: aliceblue;
  border: 2px solid #1f2235;
  background-color: #ff5823;
}
:root{
  --light-blue:#04c3FF;
  --light-blue-shadow:rgb(69,211,255/ 27%);
  --sea-green:#0ABEAD;
  --sea-green-dark:#0cbfae;
  --sea-green-extra-dark:#09bead;
  --sea-green-shadow:rgba(11,190,173,.4);
  --header-hamburger-background:rgba(11,190,173,.8);
  --orange:#f57f17;
  --dark-orange:#ff5823;
  --orane-shadow:rgba(245,127,23,.2);
  --white:#ffffff;
  --black:#333333;
  --disabled-btn:#09bead7a;
  --error-message:#e44f4fd1;
  --toast-error:#e44f4ffa ;
  --toast-shadow:#9c9c9ce3;
  --toast-success:#0cbfae;
  --loader-color:#0cbfae;
}
.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 20%;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    padding-top: 70px;
}
.profile-parent
{
    display: flex;
    align-items: center;
    color: aliceblue;
    /* padding-top: 60px; */
}
.profile-details-name{
    font-size: 24;
    font-family:Poppins SemiBold;
}
.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}
.profile-details-role h1{
    font-size: 40px;
    font-family:-webkit-body;
    color: white;

}
.primary-text{
    color: var(--white);
}
.highlighted-text{
    color:var(--dark-orange);
}
.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
   
}
.profile-options .highlighted-btn {
    margin:0 0 0 28px;
    
    
}

.profile-options .highlighted-btn:hover {
    margin:0 0 0 28px;
    background-color:orangered;
    color: #111;
}
.profile-picture{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url(/Kadambari7666.github.io/static/media/kadu1.f7452439.jfif);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
.profile-picture-background:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
    transition:1s ease-out;
}
.colz .colz-icon i{
    color: white;
    padding: 8px;
    padding-left: 0;
}
.colz .colz-icon i:hover{
    -webkit-transform:scale(1.5);
            transform:scale(1.5);
}
@media  screen and (max-width:1110px) {
    .profile-picture{
        margin: 0 0px 0 80px;
    }
    .profile-details-name{
        font-size: 12px;
    }
    .profile-details-role .primary-text{
        font-size: 20px;
    }
    .profile-options .btn{
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }
}
@media screen and (max-width:910px){
    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}
@media screen and (max-width:810px){
    .profile-picture{
        margin: 0 !important;
    }
    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options{
        display: flex;
        justify-content: space-between;

    }
    .profile-details{
        margin: 25px 0 0;
    }
    .profile-options .highlighted-btn{
        margin: 0!important;
    }
    .profile-options,.profile-details-role{
        text-align: center ;
    }
}
@media screen and (max-width:375px){
    .profile-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile-details{
        width: 90%;
    }
}
.Home-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 670px;
    /* background-color:#B0B0B0; */
    background-image: url(/Kadambari7666.github.io/static/media/img3.6015dd37.webp);
}
.a {
    height: 100vh;
    display: flex;
    align-items: center;
    background-color: white;
  }
  
  .a-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
  }
  
  .a-right {
   
    flex: 1 1;
  }
  
  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color:var(--orane-shadow);
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 70px;
    position: relative;
    overflow: hidden;
  }
  
  .a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .a-award-img {
    width: 120px;
    height: 120px;
    border-radius: 20px;
  }
  
  .a-title {
    font-weight: 400;
  }
  
  .a-sub {
    margin: 20px 0px;
  }
  
  .a-desc{
      font-weight: 300;
  }
  
  .a-award{
      margin-top: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  
  .a-award-texts{
      width: 70%;
  }
  
  .a-award-title{
      font-weight: bold;
      color: #555;
      margin-bottom: 10px;
  }
  
  @media screen and (max-width:480px) {
      .a{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
      }
  
      .a-left {
        width: 100%;
      }
  
      .a-card {
        height: 30vh;
      }
  
      .a-card.bg, .a-award {
        display: none;
      }
  
      .a-right{
        padding: 20px;
      }
  }
.c {
    height: 100vh;
    position: relative;
    background-color:grey
  }
  
  .c-bg {
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
  }
  
  .c-title{
    font-size: 41px;
  }

  .c-wrapper {
    padding: 50px;
    display: flex;
  }
  
  
  .c-right {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #D3D3D3;
  }
  
  
form {
    margin-top: 20px;
  }
  
  input {
   
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    /* background-color: grey; */
  }
  
  textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    /* background-color: grey; */
  }
  
  
  .button{
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
    cursor: pointer;
  }
  
  @media screen and (max-width: 480px) {
    .c-wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    
    
  
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 35%;
      height: 40px;
      margin: 10px;
      margin-left: 0;
    }
  
    
}
